<template>
    <div>
        <section class="slider" :style="sliderHeight">
            <Banner></Banner>
        </section>
        <section class="slider" :style="sliderHeight" id="city">
            <TitleBar title="" :url="require('../assets/city_traffic.png')" v-show="showTitle"></TitleBar>
            <Item direction="left" :url="require('../assets/city.jpg')">
                <h3>DCC EYE </h3>
                <p></p>
            </Item>
        </section>
        <section class="slider" :style="sliderHeight" id="drive">
            <TitleBar title="" :url="require('../assets/drive_assistance.png')" v-show="showTitle"></TitleBar>
            <Item direction="right" :url="require('../assets/drive.jpg')">
                <h3>ADAS</h3>
                <p>ADAS SDKADAS</p>
            </Item>
        </section>
        <section class="slider" :style="sliderHeight" id="face">
            <TitleBar title="" :url="require('../assets/micro_expression.png')" v-show="showTitle"></TitleBar>
            <Item direction="left" :url="require('../assets/face.jpg')">
                <h3>FACE CODE </h3>
                <p></p>
            </Item>
        </section>
        <section class="slider" :style="sliderHeight" id="service">
            <TitleBar title="" :url="require('../assets/car_service.png')" v-show="showTitle"></TitleBar>
            <Item direction="right" :url="require('../assets/map.jpg')">
                <h3></h3>
                <p></p>
            </Item>
        </section>
        <section class="slider" :style="sliderHeight" id="aboutUs">
            <Foot></Foot>
        </section>
    </div>
</template>
<script>
import Banner from './Banner'
import Item from './Item'
import TitleBar from './TitleBar'
import Foot from './Foot'
export default {
    name: 'app',
    components: {
        Banner,
        Item,
        TitleBar,
        Foot
    },
    data() {
        return {
            height: 0,
            width: 0,
            cnt: 0,
            slider: true
        }
    },
    methods: {

    },
    computed: {
        sliderHeight: function() {
            return this.width > 800 ? 'height:' + this.height + 'px' : 'height:' + 'auto'
        },
        showTitle: function() {
            return this.width > 800
        }
    },
    mounted() {
        this.width = window.innerWidth
        this.height = window.innerHeight
        window.addEventListener('resize', () => {
            this.width = window.innerWidth
            this.height = window.innerHeight
        })
    }
}
</script>
<style>
.slider {
    overflow: hidden;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
}
</style>



